<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">APP端分享给微信好友</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">从APP端分享给微信好友，并且以小程序的方式显示，点击分享的文案，跳到对应的小程序页面，一下是uniapp代码。</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">前提准备：</div>
            <div class="hb-a">在微信开放平台，创建APP应用，并且绑定要跳转到的小程序！否则无法跳转</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">一、APP分享给微信好友，以微信小程序形式展示</div>
            <div class="hb-a">
                <p>appShareForWxmini方法有三个实参：</p>
                <p>title：分享文案</p>
                <p>imageUrl：分享图片</p>
                <p>miniPath：微信小程序点击要跳转的路径</p>
            </div>

            <div v-highlight>
          <pre>
              <code>
                appShareForWxmini(title,imageUrl,miniPath){
                  //title 分享标题
                  //imageUrl 分享的图片
                  //miniPath 分享的路径，若有参数 通过?链接 如 /pages/index/index?a=1&b=2
                  if(!title && !imageUrl && !miniPath){
                    uni.showToast({
                      icon:'none',
                      title:"请传入正确的入参"
                    })
                    return;
                  }
                  let appFlag = false;//是否在app环境
                  // #ifdef APP-PLUS
                  appFlag = true;
                  // #endif
                  //APP环境下  才能使用
                  if(!appFlag){
                    return;
                  }
                  uni.share({
                      provider: 'weixin',
                      scene: "WXSceneSession",
                      type: 5,
                      imageUrl: imageUrl,
                      title: title,
                      miniProgram: {
                          id: 'gh_xxx',//原始ID 固定
                          path: miniPath,
                          type: 0,
                          webUrl: 'http://uniapp.dcloud.io',//随便写一个
                      },
                    success: ret => {
                      //console.log('success', ret);
                    },
                    fail: err => {
                      //console.log('error', err);
                    }
                  });
                }
              </code>
          </pre>
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">二、小程序分享，通过按键（不是点击胶囊分享）</div>
            <div class="hb-a">
                <p>miniShare方法有三个实参：</p>
                <p>title：分享文案</p>
                <p>imageUrl：分享图片</p>
                <p>miniPath：微信小程序点击要跳转的路径</p>
            </div>
            <div v-highlight>
                <pre>
                    <code>
                      miniShare(title,imageUrl,miniPath){
                        //title 分享标题
                        //imageUrl 分享的图片
                        //miniPath 分享的路径，若有参数 通过?链接 如 /pages/index/index?a=1&b=2
                        if(!title && !imageUrl && !miniPath){
                          uni.showToast({
                            icon:'none',
                            title:"请传入正确的入参"
                          })
                          return;
                        }
                        uni.share({
                          provider: "weixin",
                          scene: "WXSceneSession",
                          type: 0,
                          href: miniPath,
                          title: title,
                          imageUrl: imageUrl,
                          success: function (res) {
                            //处理成功业务
                          },
                          fail: function (err) {
                            //处理失败业务
                          }
                        });
                      }
                    </code>
                </pre>
            </div>
        </div>


    </div>
</template>

<script>
export default {
    name: 'wltxtlinkhref',
    data() {
        return {}
    },
    mounted() {
        this.doCode();
    },
    methods: {
        //一进来需要执行的
        doCode() {
            $('.view-code-btn').click(function () {
                $(this).parent().find('.hb-code').slideToggle();
            });
        },
    },
    components: {}
}
</script>
<style scoped>

</style>



